@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap);
/* @import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap"); */
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	/* font-family: "Roboto Slab", serif; */
}

/* [type="button"]:not(:disabled), */
[type="reset"]:not(:disabled),
/* [type="submit"]:not(:disabled), */
button:not(:disabled) {
	cursor: pointer;
	border: none;
	font-size: 20px;
}

.add_user {
	background-color: #000 !important;
	color: fff !important;
	box-shadow: 6px 5px gray;
}

.add_user:hover {
	border: 1px solid #000;
	background-color: #fff !important;
	color: #000 !important;
}

.leftUseList {
	display: -webkit-flex;
	display: flex;
	margin-top: 2px;
}

.leftUseList_sidebar {
	height: 100vh;
	background-color: rgb(247, 243, 243);
	border-right: 3px solid lightgray;
	/* border: 5px solid red; */
	-webkit-flex: 0.35 1;
	        flex: 0.35 1;
}

@media only screen and (max-width: 720px) {
	.leftUseList {
		width: 200px;
	}
	.leftUseList__usersname h5 {
		font-size: 1em;
	}
	.leftUseList__usersname p {
		font-size: 0.9em;
	}
}

.leftUseList__users {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	border-bottom: 1px solid #000;
	margin: 14px 10px;
}

.leftUseList__users img {
	object-fit: contain;
	border-radius: 40px;
}
.leftUseList__usersname {
	margin-left: 25px;
	width: 100%;
}

.leftUseList__users:hover {
	background-color: rgb(231, 226, 226);
}

.add_btn {
	display: -webkit-flex;
	display: flex;
	padding: 10px 10px;
}

.leftUseList_sidebar__add_button {
	display: -webkit-flex;
	display: flex;
	border: 1px solid black;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin: 10px 10px;
	padding: 10px 12px;
	/* flex: 1; */
	/* justify-content: center; */
	-webkit-align-items: center;
	        align-items: center;
	border-radius: 10px;
}

/* .del_user {
	font-size: 25px;
	margin: 10px 10px;
} */

.chatcontainer {
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
}

.chatcontainer__div {
	position: relative;
	-webkit-flex: 1 1;
	        flex: 1 1;
}

.chatcontainer__userdata {
	padding: 5px 20px;
	box-shadow: 1px 1px 1px 1px;
}

.chatcontainer__userbody {
	/* position: absolute; */
	right: 0;
	top: 10;
	display: inline-block;
	padding: 10px 15px;
	border-radius: 10px;
	margin: 10px 20px;
	background-color: #dcf8c6;
	/* background-color: rgb(214, 209, 209); */
}

.chatcontainer__userbody p {
	margin-bottom: 0rem !important;
	position: relative;
	/* 
	margin-bottom: 0rem !important; */
}
@media only screen and (max-width: 720px) {
	.chatcontainer__userbody {
		/* margin: auto; */
	}
}

.chatcontainer__usermessage {
	/* background-color: rgb(204, 201, 201); */
	position: fixed;
	right: 0;
	bottom: 0;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	color: #000 !important;
	text-align: center;
	width: 75%;
}

.message_field {
	width: 60%;
	border-radius: 35px;
	margin-bottom: 12px;
	outline: 0;
	padding: 0.5em 1em;
}

.sendIcon {
	background-color: #fff;
	/* margin-left: 15px; */
	margin-bottom: 15px;
}

.sendFile {
	margin-left: 15px;
	margin-bottom: 15px;
}

input::-webkit-input-placeholder {
	color: #000;
}

input:-ms-input-placeholder {
	color: #000;
}

input::placeholder {
	color: #000;
}

.navbar {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	padding: 5px 15px;
	/* justify-content: flex-end !important; */
	background-color: #01ab97;
	color: white;
}

.navbar img {
	width: 80px;
	border-radius: 30px;
	object-fit: contain;
}

.navbar h1 {
	font-family: "Roboto Slab", serif;
	margin: 10px 35px;
}

.app {
	display: grid;
	place-items: center;
	background-color: #ededed;
	height: 100vh;
}

.app__body {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	/* background-color: #ededed; */
	height: 100vh;
	width: 100vw;
}

body {
	overflow-x: hidden; /* Hide scrollbars */
}

